$color-sign: #000;
.sign {
  height: 100vh;
  background: $color-sign;

  &__container {
    display: flex;
    height: 90vh;
  }

  &__img {
    width: 50%;
    display: flex;
    background-image: url(./img/sign__bg.png);
    background-repeat: no-repeat;
    background-position: center;
  }
  &__content {
    width: 50%;
  }
  &__svg {
    

    &_primory {
      height: 225px;
      fill: white;
      margin: auto;
    }
    &_seccond {
      height: 45px;
      fill: red;
    }
  }
  &__footer {
    display: flex;
    &-link {
      
    }
  }
}